import React from 'react';
import Button from '../../components/button';

const Card = props => {
	return (

		<div className="border p-5 w-1/2 rounded rounded-lg flex flex-col lg:flex-row shadow-lg justify-center items-center">
			<div className="flex justify-center items-center lg:mr-20 mr-0 mb-10">
				<img 
					src={props.img} 
					className="w-40 rounded rounded-full object-cover"
					alt="menininha"
				/>
			</div>
			<div className="p-2 lg:p-0 lg:w-2/3">
				<div>
					<p className="uppercase font-semibold text-3xl mb-3 text-teal-400 lg:text-left text-center">{props.title}</p>
					<p className="text-gray-900 text-base w-full font-sans p-2">
				   		{props.text}
					</p>
				</div>
				<div className="flex w-full flex-col sm:flex-row">
					<Button 
						style="outline"
						content="Sign"
						bg="bg-green-400"
					/>
					<Button 
						style="outline-none"
						content="Sign Out"
					/>
				</div>
			</div>
		</div>

	);
}

export default Card;
